'use strict';
import {EventTarget} from "../common/event";
import {SynchronousJsonPortMarshaler} from "./marshaler";
import {ISettingsProvider} from "../interfaces/iSettingsProvider";
import {Promise} from "q";
export class SettingsMarshaler extends SynchronousJsonPortMarshaler {

    constructor(control: EventTarget, public settingsProvider: ISettingsProvider) {
        super(control);
    }

    public get(collection: string, keys: string[]): Promise<any> {
        return this.settingsProvider.get(collection, keys);
    }

    public set(collection: string, toSet: any): void {
        this.settingsProvider.set(collection, toSet);
    }
}